import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { getRecipes, getDiets, getRecipesByDiet, getRecipesByName } from "../../Redux/Actions";
import CardsContainer from "../../Components/CardsContainer/CardsContainers";
import { useSelector } from "react-redux";

export default function Home() {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(getRecipes())
    dispatch(getDiets());
  }, [dispatch]);
  
  const diets = useSelector((state) => state.dietsList);
  let count = 0;
  // console.log("diets", diets)
  
const [form, setForm] = useState({
  diet: "",
  title: ""
})

const changeHandler = (event) => {
  const property = event.target.name;
  const value = event.target.value;
  setForm({ ...form, [property]: value });
  //setErrors(validate({ ...form, [property]: value }));
};

const submitHandler = (e) => {
  e.preventDefault();
  if (form.title) {
    getRecipesByName(form.title)
  } else if (form.diet) {
    getRecipesByDiet(form.diet)
  } else alert ("You must select a diet type or entry the recipe name.")
  
};


  return (
    <div>
      <h1> HOME </h1>
      
      <form onSubmit={(e) => submitHandler(e)}>
        <label>Find for recipe name: </label>
        <input
        placeholder="Entry the recipe name to found..."
        name= "title"
        type="text"
        value={form.title}
        onChange={changeHandler}
        />
      <label >Find for diet type: </label>
      <select 
      name="diet" 
      id="dietslist" 
      size="1"
      value={form.diet}
      onChange={changeHandler}
      placeholder="Select a diet to found..."
      >
        {diets &&
          diets.map((d) => (
            <option key={count++} value={d}>
              {d}
            </option>
          ))}
      </select>
      <button type="Submit"> FIND </button>
      </form>
      <CardsContainer 
      id= {count++}
      diet={form.diet}
      title={form.title}
      />
    </div>
  );
}
