import React, { useEffect } from "react";
//import CardDetail from "../../Components/CardDetail/CardDetail";
import { useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import { useDispatch } from "react-redux";
import { getRecipeById } from "../../Redux/Actions";

export default function Detail() {
  const dispatch = useDispatch();
  let { id } = useParams();
  let counterDts = 1000
  let counterSteps = 1

  useEffect(() => {
    dispatch(getRecipeById(id));
  }, [dispatch, id]);

  let recipeDetail = useSelector((state) => state.recipeDetail);


  return (
    <div>
      <h1> DETALLE DE LA RECETA </h1>
      <h3> {recipeDetail.title} </h3>
      <img src={recipeDetail.image} alt="NOT FOUND"></img>
      <h4> Nivel de salud (1-100): {recipeDetail.healthScore} </h4>
      <h4> Dietas que aplican: </h4>
      {recipeDetail.steps && recipeDetail.diets.map((d) => <li key={counterDts++}>{d}</li>)}
      <h5> Resumen del plato: {recipeDetail.summary} </h5>
      <h6> Preparación: 
        {recipeDetail.steps && recipeDetail.steps.map(d=><p key={counterSteps++}>Paso N°{d.number}: {d.step}</p>)} </h6>     
    </div>
  );
}
